.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.login {
  position: absolute;
  left: 67%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
@media screen and (max-width: 1400px) {
  .login {
    left: 50%;
    top: 50%;
  }
}
.login .login-form {
  max-width: 300px;
}
.login .login-form-forgot {
  float: right;
}
.login .login-form-button {
  opacity: 0.8;
  width: 100%;
}
.login .ant-input {
  border-color: transparent;
  color: var(--text-color);
  text-indent: 8px;
}
.login .ant-input-prefix .anticon {
  color: var(--primary);
}
.login .loginTitle {
  font-size: 18px;
  margin-bottom: 8px;
}
.login .loginTitle span {
  color: var(--primary);
}
@media screen and (min-width: 1400px) {
  .login .loginTitle {
    position: absolute;
    font-size: 38px;
    font-weight: bold;
    left: -400px;
  }
}
